<template>
  <div class="saved-apartments">

       <yd-navbar title="Saved">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon >back</yd-navbar-back-icon>
            </router-link>

             
            <yd-icon slot="right" name="share2" @click.native="contact = true" size='30px'></yd-icon>           
        </yd-navbar>

        
           {{apartment_num}} 


        <el-row :gutter="10" class="item">
    <el-col class="item" :xs="12" :sm="12" :md="6" :lg="6" :xl="6" v-for="(o, index) in favlist" :key="index">
        <el-card :body-style="{ padding: '0px' }">
            <router-link :to="'/details/' + o.id">

            <!-- <div class="image" v-for="item,index in o.images.slice(0,1)" :key='index'>
                            <img :src='item.image' />               
            </div> -->

            </router-link>
            <div style="padding: 0 10px 10px 10px;">
                <span style="font-size:18px">{{o.compound}}</span>
                <div class="bottom clearfix">
                <time class="time">Price：{{o.price}} </time>
                <div class="time">{{o.rooms_num}} Bed 
                {{o.bath_num}} Bath</div>

                <time class="time">Add time{{o.add_time}} </time>


                </div>
            </div>
        </el-card>
    </el-col>
</el-row>

<yd-popup v-model="contact" position="center" width="90%" class="pop">
            <div style="background-color:#fff; padding:10px 10px 25px 10px">

<h1 class="bigtitle">Submit my list</h1>
                 <yd-cell-group>

        <yd-cell-item >
            <span slot="left" class="lable">Name :&nbsp;</span>
            <yd-input slot="right"  v-model="yourname" placeholder="" class="lable"></yd-input>
        </yd-cell-item>

        <yd-cell-item>
            <span slot="left" class="lable">Phone :&nbsp;</span>
            <yd-input slot="right" v-model="phone" regex="mobile" placeholder="" class="lable"></yd-input>
        </yd-cell-item>

         <yd-cell-item>
            <span slot="left" class="lable">Wechat :&nbsp;</span>
            <yd-input class="lable" slot="right" v-model="wechat" placeholder="" ></yd-input>
        </yd-cell-item>
        
      

        <yd-cell-item>
            <yd-textarea slot="right" placeholder="more words" maxlength="200" v-model="message" class="lable"></yd-textarea>
        </yd-cell-item>

        
        
    </yd-cell-group>

    <yd-button-group>
            <yd-button size="large" @click.native="SubmitMyfavlist">Send</yd-button>
        </yd-button-group>

            </div>
        </yd-popup>


  </div>
</template>
<script>
import {APISubmitInfo} from '../../api/api'
import {mapGetters} from 'vuex'
export default {
        data() {
        return{
            contact: false,
            yourname:'',
            phone:'',
            wechat:'',
            message:'',
            apartment_num:'',
        }
    },
    methods:{
        GetAptIDs(){
            console.log(this.favlist)
            this.favlist.forEach(element => {
                this.apartment_num = this.apartment_num + element.id + ','
                
            })

        },
        SubmitMyfavlist(){
            APISubmitInfo(
                {
                    name:this.yourname,
                    mobile:this.phone,
                    wechat:this.wechat,
                    message:this.message,
                    apartment:this.apartment_num
                }
                
            ).then(
                res => {
                    console.log(res.data)
                    this.list = res.data
                }
            ).catch(err => {console.log(err)})
        }
    },
    created(){
        this.GetAptIDs()
    },
    computed:{
        ...mapGetters(['favlist']),
    },

}
</script>

<style lang="stylus" scoped>
.saved-apartments
    .pop
        .lable
            font-size 18px
            textarea
                font-size 18px!important
            input 
                font-size 18px!important
                padding-left 10px
    .item
        img
            width 100%
</style>



